import ScrollOut from "scroll-out";
import "./form";

let prevScrollpos = Math.max(0, window.pageYOffset);
window.onscroll = () => {
  /** Nav Bar Show/Hide */
  let currentScrollPos = Math.max(0, window.pageYOffset);
  if (prevScrollpos >= currentScrollPos) {
    document.getElementById("navbar").style.top = "0";
  } else {
    document.getElementById("navbar").style.top = "-7rem";
  }
  prevScrollpos = currentScrollPos;

  /** Contact Card Show/Hide */
  if (
    document.body.scrollTop > 500 ||
    document.documentElement.scrollTop > 500
  ) {
    document.getElementById("contactCard").style.cssText =
      "height: 8rem;opacity: 0.9";
    document.querySelector("#contactCard svg").style.animation =
      "shake 0.5s 2s 5";
    document.getElementById("goUp").style.cssText = "opacity: 1";
    document.getElementById("navbar").classList.add("shadow-md");
  } else {
    document.getElementById("contactCard").style.cssText =
      "height: 0;opacity: 0;";
    document.querySelector("#contactCard svg").style.animation = "none";
    document.getElementById("goUp").style.cssText = "opacity: 0";
    document.getElementById("navbar").classList.remove("shadow-md");
  }
};

/** Handburger Animation */
const menuBtn = document.querySelector(".menu-btn");
menuBtn.addEventListener("click", () => {
  menuBtn.classList.toggle("open");
  document.querySelector(".cover").classList.toggle("hidden");
  document.body.classList.toggle("sidenav-active");
  document.body.classList.toggle("noscroll");
});

ScrollOut({
  once: true,
  threshold: 0.2,
});
