const mapURL =
  "https://maps.google.com/maps?width=100%&height=600&hl=en&q=%C4%B0zmir+(My%20Business%20Name)&ie=UTF8&t=&z=14&iwloc=B&output=embed";

const loadMap = () => {
  const frameWindow = document.getElementById("googleMap").contentWindow;
  setTimeout(function () {
    frameWindow.location.replace(mapURL);
  }, 2000);
};

/** When page completly loads */
window.addEventListener("load", () => {
  setTimeout(() => {
    document.getElementById("preloader").classList.add("loaded");
    document.querySelector("body").classList.remove("loading");
  }, 100);
  /** Load Map */
  loadMap();
});
